import { ADD_NEWS, GET_NEWS, GET_SINGLE_NEWS } from "../types/newsTypes";
import instance from "../utils/config";


export const getNews = () => async (dispatch, token) => {
  try {
    const res = await instance.get("/api/news");
    dispatch({ type: GET_NEWS, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};

export const getNewsForApproval = () => async (dispatch, token) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };
  try {
    const res = await instance.get("/api/news/approval", config);

    dispatch({ type: GET_NEWS, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};

export const getNewsById = (id) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };
  try {
    const res = await instance.get(`/api/news/${id}`, config);

    dispatch({ type: GET_SINGLE_NEWS, payload: res.data.news });
  } catch (err) {
    console.error(err);
  }
};

export const addNews = (formData) => async (dispatch, token) => {
  const config = {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  };
  
  try {
    const res = await instance.post("/api/news", formData, config);
    dispatch(getNews());
  } catch (err) {
    console.error(err);
  }
};

export const deleteNews = (id) => async (dispatch) => {
  try {
    const res = await instance.delete(`/api/news/${id}`);

    dispatch(getNews());
  } catch (err) {
    console.error(err);
  }
};

export const updateNews = (formData, id) => async (dispatch, token) => {
  
    // for (var value of formData.values()) {
    //   console.log('frooom data', value);
    // }
    const config = {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    };
  try {
    const res = await instance.put(`/api/news/${id}`, formData, config);
    dispatch(getNews());
  } catch (err) {
    console.error(err);
  }
};

export const approveUserNews = (id) => async (dispatch, token) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };
  try {
    
    const body = {
      status: "Accepted",
    };
    const res = await instance.put(`/api/news/${id}`, body, config);
      console.log('res', res);
    dispatch(getNews());
  } catch (err) {
    console.error(err);
  }
};

export const denyUserNews = (id) => async (dispatch) => {
  try {
    const config = {
      headers: {
        "Content-Type": "Application/json",
      },
    };
    const body = {
      status: "Denied",
    };
    const res = await instance.put(`/api/news/${id}`, body, config);

    dispatch(getNews());
  } catch (err) {
    console.error(err);
  }
};
