import React, { useState, useEffect } from "react";
import "../../../styles/News.css";
import "../../../styles/Modal.css";
import { useDispatch, useSelector } from "react-redux";
import SaveButton from "../buttons/SaveButton";
import ViewButton from "../buttons/ViewButton";
import { useNavigate, useParams } from "react-router-dom";
import { fetchCategories } from "../../../actions/categoryActions";
import { addChannel } from "../../../actions/channelsAction";


const NewsModal = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const storedState = useSelector((state) => state);
  useEffect(() => {
    if (!storedState?.categories) dispatch(fetchCategories());
  }, []);
  const closeModal = () => {
    navigate("/channels");
  };

  const [form, setForm] = useState({ title: "", description: "", category: "" });
  const [picture, setPicture] = useState(null);

  const handleChange = (e) => {
    console.log('e.target.value', e.target.value);
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const onChangePicture = (e) => {
    setPicture(e.target.files[0]);
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('title', form.title);
    formData.append('description', form.description);
    if (picture !== null) formData.append('picture', picture, picture.name);
    formData.append('category', form.category);

    if (form.category === null) {
      form.category = storedState.categories[0]._id;
    }
  dispatch(addChannel(formData));
  navigate("/channels");
  };

  return (
    <div className="modal-container">
      <div className="modal-wrapper">
        <ViewButton
          handleClick={closeModal}
          text="Go Back"
          className="go-back-btn"
        />
        <h3 className="modal-title">
         Create Channel
        </h3>
     
        <div className="form-group" style={{ border: '1px' }}>
        <div className="upper-div" style={{ display: 'grid', alignItems: 'center' }}>
          <label htmlFor="" style={{ marginBottom: '5px' }}>Upload Channel Image</label>
          <input type="file" name="upload-photo" onChange={onChangePicture}  />
        </div>
        </div>
        <input
          name="title"
          type="text"
          placeholder="Name of Channel"
          className="modal-input mt-0 mb-0"
          onChange={handleChange}
          value={form?.title}
        />
        <select name="category" className="select-box mt-0 mb-0" onChange={handleChange}>
        {storedState?.categories?.map((item) => {
            return (
              <option
                value={item._id}
              >
                {item.value}
              </option>
            );
          })}
        </select>
        <textarea  onChange={handleChange} value={form?.description}  name="description" type="text" placeholder="description" className="modal-input mt-0 mb-0"></textarea>
        <SaveButton  handleClick={handleSubmit} />
      </div>
    </div>
  );
};

export default NewsModal;
