import React, { useEffect } from "react";
import SaveButton from "../buttons/SaveButton";
import ViewButton from "../buttons/ViewButton";
import { fetchCategories } from "../../../actions/categoryActions";
import { useDispatch, useSelector } from "react-redux";
// import { useNavigate } from "react-router-dom";

const AdvertisementsModal = () => {
  // const navigate = useNavigate();
  // const closeModal = () => {
  //   navigate("/memberships");
  // };
  const dispatch = useDispatch();
  const storedState = useSelector((state) => state);
  useEffect(() => {
    if (!storedState.categories) dispatch(fetchCategories());
  }, []);

  return (
     <>
     <div className="modal-container">
      <div className="modal-wrapper" style={{ justifyContent: 'center' }}>
        <ViewButton
        // handleClick={closeModal}
          text="Go Back"
          className="go-back-btn"
        />
        <h3 className="modal-title">Advertisements</h3>
        <select className="select-box">
            <option>Select Location</option>
            <option>Home Top</option>
            <option>Home Bottom</option>
          </select>
        <select className="select-box">
            <option>Select Category</option>
            {storedState?.categories?.map((item) => {
              return <option key={item?._id}>{item?.value}</option>
            })}
          </select>
        <textarea
          name="content"
          type="text"
          placeholder="Enter Content"
          className="modal-textarea"
        ></textarea>
        <SaveButton />
      </div>
    </div>
     </>
  );
};

export default AdvertisementsModal;
