import axios from "axios";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import AddButton from "../buttons/AddButton";
import instance from "../../../utils/config";
import { BaseURL } from "../../../utils/baseUrl";
import { useParams } from 'react-router-dom';
import Switch from "react-switch";
import { disableVideo } from "../../../actions/channelsAction";
import { useDispatch, useSelector } from "react-redux";
import { getChannels, getVideos } from "../../../actions/channelsAction";
import { useNavigate } from "react-router-dom";

const ChannelDetail = () => {

  const dispatch = useDispatch();
  const [videos, setVideos] = useState([]);
  const [channelName, setChannelName] = useState('');
  const [videoDate, setVideoDate] = useState([]);
  const [user, setUser] = useState();
  const params = useParams();
  const navigate = useNavigate();

  const headers = {
    "Content-Type": "Application/json",
  }


  const channels = useSelector(state => state?.channels?.channel ?? []);
  const videoss = useSelector((state) => state?.channels?.video ?? []);

  const userId = useSelector((state) => state?.userDetails?._id ?? '');
  
  useEffect(() => {
    dispatch(getChannels());
    dispatch(getVideos());
    VideoDate()
  }, []);
  useEffect(() => {
    channels.map(elementTwo => {
      if (elementTwo?._id === params?.channelId) {
        setChannelName(elementTwo?.title);
      }
    });
  }, [channels]);
  useEffect(() => {
    channels.find(elementThree => {
      if (elementThree?._id === params?.channelId) {
        setUser(elementThree?.user?._id);
      }
    });
  }, [channels]);


  useEffect(() => {
    setVideos(videoss);
  }, [videoss]);

  function VideoDate() {
    videos?.map(elementOne => {
      if (elementOne?.channel === params?.channelId) {
        var myDate = new Date(elementOne?.date);
        var year = myDate.getFullYear();
        var month = myDate.getMonth() + 1;
        var date = myDate.getDate();
        var finalDate = date + '-' + month + '-' + year;
        setVideoDate(finalDate);
      }
    });
  }

  
  const handleChange = (checked, id) => {
    // console.log("Checked", checked);
    const localVideos = [ ...videos ];
    const index = localVideos.findIndex(video => video._id === id)
    // console.log("Checked", index, localVideos);
    localVideos[index]['disabled'] = !checked;
    setVideos(localVideos);
    dispatch(disableVideo({ disabled: !checked }, id));
  };

  const handleClick = () => {
    navigate(`/channel/${params?.channelId}/video/modal`);
  };

  return (
    <>
      <div className="channels-container">
        <div className="channels-list">
          <div className="channels-list-header" style={{ borderBottom: "1px solid", marginBottom: "28px", paddingBottom: "16px" }}>
            <h3>{channelName}</h3>
            {
              userId === user 
              ?
              <AddButton handleClick={handleClick} resource="Video" />
              :
              ' '
            }
          </div>
          <table>
            <thead>
              <tr>
                <th>S.No.</th>
                <th>Video</th>
                <th></th>
                <th>Date</th>
                <th>Views</th>
                <th>Comments</th>
                <th>Likes</th>
                <th>Disable/Enable</th>
              </tr>
            </thead>
            <tbody>
              {
                videos.map((item, index) => {
                  if (item?.channel === params?.channelId)
                    return <tr key={item._id}>
                      <td>{index + 1}</td>
                      <td><img src="" /></td>
                      <td>{item?.title}</td>
                      <td>{videoDate}</td>
                      <td>2</td>
                      <td>4</td>
                      <td>5</td>
                      <td>
                        <Switch
                          onChange={(checked) => handleChange(checked, item?._id)}
                          checked={!item?.disabled}
                          className="react-switch"
                        />
                      </td>
                    </tr>
                })
              }
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};

export default ChannelDetail;
