import React, { useState, useEffect } from "react";
import "../../../styles/News.css";
import { useDispatch, useSelector } from "react-redux";
import { getChannels } from "../../../actions/channelsAction";
import FilterBar from "../FilterBar";
import { Link } from "react-router-dom"

const ChannelsList = () => {
  const filterOptions = [
    {
      title: "Channel Name",
      value: "name",
    },
  ];
  const dispatch = useDispatch();

  const [filterValue, setFilterValue] = useState(filterOptions[0].value);
  const [filterText, setFilterText] = useState("");

  // const state = useSelector((state) => state);
  const channels = useSelector(state => state?.channels?.channel ?? [] );

  useEffect(() => {
    dispatch(getChannels());
  }, []);

  // let channelss = channels?.channel

  if (filterText && channels !== undefined) {
    channels = channels.filter((item) =>
      item[filterValue]?.toLowerCase().includes(filterText)
    );
  }


  return (
    <>
      <FilterBar
        filterOptions={filterOptions}
        filterValue={filterValue}
        filterText={filterText}
        setFilterText={setFilterText}
        setFilterValue={setFilterValue}
      />
      <table>
        <thead>
          <tr>
            <th>S.NO.</th>
            <th>Name</th>
            <th>Videos</th>
            <th>User</th>
          </tr>
        </thead>
        <tbody>
            {channels.map((item, index) => {
            return (
              <tr key={item._id}>
                <td>{index + 1}</td>
                <td><Link to={`/channel/${item._id}`}>{item?.title}</Link></td>
                {/* <td><Link to={`/channels/${item._id}`}>{item?.title}</Link></td> */}
                <td>{item?.videos?.length}</td>
                <td>{item?.user?.firstname + " " + item?.user?.lastname}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  );
};

export default ChannelsList;
