import React from "react";
import "../../../styles/Buttons.css";

const SaveButton = ({ handleClick, style, hiddenBtn }) => {
  return (
    <button className={hiddenBtn === true ? 'save-button' : 'save-button Notsee'} onClick={handleClick} style={{...style}}>
      Save
    </button>
  );
};

export default SaveButton;
