import React, { useState, useEffect, useCallback } from "react";
import "quill/dist/quill.snow.css";
import "../../../styles/News.css";
import "../../../styles/Modal.css";
import { useDispatch, useSelector } from "react-redux";
import { addNews, getNewsById, updateNews } from "../../../actions/newsAction";
import Quill from "quill";
import SaveButton from "../buttons/SaveButton";
import ViewButton from "../buttons/ViewButton";
import TextEditor from "../news/TextEditor";
import { useNavigate, useParams } from "react-router-dom";
import { fetchCategories } from "../../../actions/categoryActions";
import { TiTimes } from "react-icons/ti";
import { imageBaseURL } from "../../../utils/imageBaseUrl";
import { useToasts } from 'react-toast-notifications';

const NewsModal = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const params = useParams();
  const { addToast } = useToasts();

  const [quill, setQuill] = useState("");
  const [quill2, setQuill2] = useState("");

  const wrapperRef = useCallback((wrapper) => {
    if (wrapper === null) {
      return;
    }
    wrapper.innerHTML = "";
    const editor = document.createElement("div");
    wrapper.append(editor);

    const toolbarOptions = [
      ["bold", "italic", "underline"],
      [{ list: "ordered" }, { list: "bullet" }],
    ];

    const q = new Quill(editor, {
      theme: "snow",
      modules: {
        toolbar: toolbarOptions,
      },
      placeholder: "Enter description",
    });

    setQuill(q);

    // if (desc) quill.root.innerHTML = desc;
  }, []);

  const wrapperRef2 = useCallback((wrapper) => {
    if (wrapper === null) {
      return;
    }
    wrapper.innerHTML = "";
    const editor = document.createElement("div");
    wrapper.append(editor);

    const toolbarOptions = [
      ["bold", "italic", "underline"],
      [{ list: "ordered" }, { list: "bullet" }],
    ];

    const q = new Quill(editor, {
      theme: "snow",
      modules: {
        toolbar: toolbarOptions,
      },
      placeholder: "Enter description",
    });

    setQuill2(q);

  }, []);

  const [form, setForm] = useState({ title: "", author: "", desc: "", desc2: "", image: "", country: "", city: "", state: "" });
  const [tag, setTag] = useState("");
  const [picture, setPicture] = useState(null);
  const [secondpicture, setSecondPicture] = useState(null);
  const [updateData, setUpdateData] = useState(false);
  const [hiddenBtn, setHiddenBtn] = useState(true);
console.log('hiddenBtn', form);
  const storedState = useSelector((state) => state);
  // console.log('storedState', picture);
  // console.log('storedState', storedState);
  useEffect(() => {
    if (!storedState.categories) dispatch(fetchCategories());
    const fetchNewsData = async () => {
      dispatch(getNewsById(params.id));
    };
    if (params.id) {
      fetchNewsData();
      setUpdateData(true);
    }
  }, []);

  useEffect(() => {
    if (params.id) {
      const data = storedState?.news?.news_detail;
      setForm({ ...data, category: data?.category?._id });
      console.log('det', storedState?.news?.news_detail);
    }
  }, [storedState?.news?.news_detail]);

  // const { title, author, desc, desc2, image, country, city, state } = form;

  const [tags, setTags] = useState([]);
  const handleTags = (e) => {
    setTags([...tags, tag]);
    setTag("");
  };

  useEffect(() => {
    if (form?.desc) quill.root.innerHTML = form?.desc;
    if (form?.desc2) quill2.root.innerHTML = form?.desc2;
  }, [form?.desc, form?.desc2]);

  const handleChange = (e) => {
    console.log('e.target.value', e.target.value);
    if (e.target.name === "tag") {
      setTag(e.target.value);
    } else setForm({ ...form, [e.target.name]: e.target.value });
  };

  const onFirstChangePicture = (e) => {
    setPicture(e.target.files[0]);
  }

  const onSecondChangePicture = (e) => {
    // console.log('e.target.files[0]', e.target.files[0]);
    setSecondPicture(e.target.files[0]);
  }

  const handleSubmit = (e) => {
    e.preventDefault();
   
    form.desc = quill.root.innerHTML;
    form.desc2 = quill2.root.innerHTML;
    form.tags = tags;
    
    const formData = new FormData();
    formData.append('title', form.title);
    formData.append('author', form.author);
    formData.append('desc', form.desc);
    formData.append('desc2', form.desc2);
    if (picture !== null) formData.append('picture', picture, picture.name);
    if (secondpicture !== null) formData.append('secondpicture', secondpicture, secondpicture.name);
    formData.append('country', form.country);
    formData.append('state', form.state);
    formData.append('city', form.city);
    if (!params.id) formData.append('category', form.category);
    if (params.id) formData.append('category', form.category);

   
    if (form.category === null) {
      form.category = storedState.categories[0]._id;
    }
    if (!updateData) {
      dispatch(addNews(formData));
      addToast('News Created Successfully', { appearance: 'success', autoDismiss: true, autoDismissTimeout: 3000 });
    }
   
    else {
      dispatch(updateNews(formData, params.id));
    }
    navigate("/news");
    setHiddenBtn(false);
  };

  const closeModal = () => {
    navigate("/news");
  };

  return (
    <div className="modal-container">
      <div className="modal-wrapper">
        <ViewButton
          handleClick={closeModal}
          text="Go Back"
          className="go-back-btn"
        />
        <h3 className="modal-title">
          {updateData === false ? "Add" : "Update"} News
        </h3>
        {/* {
          picture !== null ?
            <img src={URL.createObjectURL(picture)} alt="file" style={{ maxWidth: "200px", minWidth: "200px" }} />
            :
            ' '
        }
        {
          updateData !== false && picture === null ?
            <img src={imageBaseURL + storedState?.news?.news_detail?.picture} alt="file" style={{ maxWidth: "200px", minWidth: "200px" }} />
            :
            ' '
        } */}
        <div className="form-group" style={{ border: '1px' }}>
        <div className="upper-div" style={{ display: 'grid', alignItems: 'center' }}>
          <label htmlFor="" style={{ marginBottom: '5px' }}>Upload Head Image</label>
          <input type="file" name="upload-photo" onChange={onFirstChangePicture} />
        </div>
        <div className="img-div">
        {
          picture !== null ?
            <img src={URL.createObjectURL(picture)} alt="file" style={{ maxWidth: "90px", minWidth: "90px" }} />
            :
            ' '
        }
        {
          updateData !== false && picture === null ?
            <img src={imageBaseURL + storedState?.news?.news_detail?.picture} alt="file" style={{ maxWidth: "90px", minWidth: "90px" }} />
            :
            ' '
        }
        </div>
        </div>
        <input
          name="title"
          type="text"
          placeholder="Enter title"
          className="modal-input"
          onChange={handleChange}
          value={form?.title}
        />
        <input
          name="author"
          type="text"
          placeholder="Enter Author name"
          className="modal-input"
          onChange={handleChange}
          value={form?.author}
        />
        <select name="category" className="select-box" onChange={handleChange}>
          {storedState?.categories?.map((item) => {
            return (
              <option
                value={item._id}
                // selected={form?.category?._id === item._id}
              >
                {item.value}
              </option>
            );
          })}
        </select>
        <div className="form-group" style={{ border: '1px' }}>
        <div className="upper-div" style={{ display: 'grid', alignItems: 'center' }}>
          <label htmlFor="" style={{ marginBottom: '5px' }}>Upload Description 1 Image</label>
          <input type="file" name="upload-photoo" onChange={onSecondChangePicture} />
        </div>
        <div className="img-div">
        {
          secondpicture !== null ?
            <img src={URL.createObjectURL(secondpicture)} alt="file" style={{ maxWidth: "90px", minWidth: "90px" }} />
            :
            ' '
        }
        {
          updateData !== false && secondpicture === null ?
            <img src={imageBaseURL + storedState?.news?.news_detail?.secondpicture} alt="file" style={{ maxWidth: "90px", minWidth: "90px" }} />
            :
            ' '
        }
        </div>
        </div>
        <div className="text-editor-container">
          <div ref={wrapperRef} className="text-editor-wrapper"></div>
        </div>
        <div className="text-editor-container">
          <div ref={wrapperRef2} className="text-editor-wrapper"></div>
        </div>
        <div className="tag-list">
          {tags.map((tag) => {
            return (
              <div className="tags">
                <div>{tag}</div>
              </div>
            );
          })}
        </div>
        <input
          name="country"
          type="text"
          placeholder="Country"
          className="modal-input"
          value={form?.country}
          onChange={handleChange}
        />
        <input
          name="state"
          type="text"
          placeholder="State"
          className="modal-input"
          value={form?.state}
          onChange={handleChange}
        />
        <input
          name="city"
          type="text"
          placeholder="City"
          className="modal-input"
          value={form?.city}
          onChange={handleChange}
        />
        <SaveButton hiddenBtn={hiddenBtn}  handleClick={handleSubmit} />
      </div>
    </div>
  );
};

export default NewsModal;
