import { GET_CHANNELS, DISABLE_VIDEO, GET_VIDEOS } from "../types/newsTypes";

export const channelsReducer = (state = null, action) => {
  switch (action.type) {
    case GET_CHANNELS:
      return {...state, channel: action.payload};
    case GET_VIDEOS:
      return {...state, video: action.payload};
    default:
      return state;
  }
};
