import instance from "../utils/config";
import { GET_CHANNELS, GET_VIDEOS } from "../types/newsTypes";

export const getChannels = () => async (dispatch) => {
  try {
    const res = await instance.get("/api/channels");

    dispatch({ type: GET_CHANNELS, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};

export const getVideos = () => async (dispatch) => {
  try {
    const res = await instance.get("/api/channels/videos/all");

    dispatch({ type: GET_VIDEOS, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};

export const addChannel = (formData) => async (dispatch, token) => {
  const config = {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  };
  try {
    const res = await instance.post("/api/channels", formData, config);
    dispatch(getChannels());
  } catch (err) {
    console.error(err);
  }
};
export const uploadVideo = (formData) => async (dispatch, token) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };
  try {
    const res = await instance.post("/api/channels/video", formData, config);
    dispatch(getChannels());
  } catch (err) {
    console.error(err);
  }
};

