import React, { useState, useEffect } from "react";
import "../../../styles/Channels.css";

import { useDispatch, useSelector } from "react-redux";

import ChannelsList from "./ChannelsList";

import { useNavigate } from "react-router-dom";
import AddButton from "../buttons/AddButton";

const ChannelsSection = () => {
  const dispatch = useDispatch();

  const navigate = useNavigate();
  const handleClick = () => {
    navigate("/channels/modal");
  };

  const state = useSelector((state) => state);

  
  return (
    <div className="channels-container">
      <div className="channels-list">
        <div className="channels-list-header" style={{borderBottom: "1px solid", marginBottom: "28px", paddingBottom: "16px"}}>
          <h3>Channels</h3>
          <AddButton resource="Channel" handleClick={handleClick} />
        </div>

        <ChannelsList channels={state.channels} />
      </div>
    </div>
  );
};

export default ChannelsSection;
